/* src/styles/custom.css - refactored for Tailwind v3 design system */

/* Keep compatibility class names used across the app, but rely on Tailwind utilities */

.card {
  @apply bg-white/80 backdrop-blur-sm rounded-xl shadow-soft-lg p-6;
}

.btn-primary {
  @apply inline-flex items-center justify-center gap-2 px-4 py-2 rounded-lg font-semibold text-white
         bg-gradient-to-r from-brand-blue via-brand-purple to-brand-pink
         transform hover:scale-105 hover:shadow-lg transition-all duration-300;
}

.btn-danger {
  @apply inline-flex items-center justify-center gap-2 px-4 py-2 rounded-lg font-semibold text-white
         bg-gradient-to-r from-brand-red via-brand-red to-brand-orange
         transform hover:scale-105 hover:shadow-lg transition-all duration-300;
}

/* Theme helpers - apply to top-level container (body/main) */
.theme-default {
  @apply bg-gradient-theme-default text-gray-900;
}

.theme-red {
  @apply bg-gradient-theme-red text-white;
}

/* Utility: subtle accessible focus */
.focus-ring {
  @apply focus:outline-none focus:ring-4 focus:ring-primary/20;
}

/* Small responsive container utility used by pages */
.container-max {
  @apply max-w-6xl mx-auto px-4;
}

/* Typographic helpers */
.h1 { @apply text-3xl md:text-4xl font-semibold text-textPrimary; }
.h2 { @apply text-2xl font-semibold text-textPrimary; }
.p-muted { @apply text-sm text-muted; }


